
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { mapGetters, mapActions } from 'vuex'
export default {
  components: {
    Group,
    Checklist,
    XButton,
    Divider
  },
  data () {
    return {
      passengers: [],
      chkTickets: [],
      sendThkTickets: []
    }
  },
  computed: {
    ...mapGetters([
      'TicketInfos',
      'QueryStationChk'
    ])
  },
  methods: {
  	...mapActions([
  	  'GetTicketChk'
  	]),
  	change (val) { // 组合验票码
  	  let arr = [];
  	  val.forEach(el => {
  	    this.TicketInfos.forEach(el2 => {
  	      if(el == el2.orderId) {
  	        var obj = {
  	          orderId: el2.orderId,
  	          ticketCode: el2.ticketCode
  	        }
  	        arr.push(obj)
  	      }
  	    })
  	  })
  	  this.sendThkTickets = arr
  	},
    submitChk () { // 提交验票
      if(this.sendThkTickets.length > 0) {
        const params = {
          shiftId: this.QueryStationChk.shiftId,
          stationId: this.QueryStationChk.stationId,
          ticketInfos: this.sendThkTickets
        }
        this.GetTicketChk(params).then(res => {
          this.$vux.toast.show({
      	    type: 'text',
            text: res.status.desc
          })
          if(res.status.code === 1) {
            this.$router.go(-1)
          }
        })
      }else {
        this.$vux.toast.show({
      	  type: 'text',
          text: '请选择验票乘客',
          width: '10.6em'
        })
      }
    }
  },
  created () {
  	console.log(this.TicketInfos)
    this.TicketInfos.forEach(el => {
      let obj = {
        key: el.orderId,
        value: `${el.name} ${el.partOfPhone}`
      }
      if(obj.key !==undefined ) {
      	this.chkTickets.push(el.orderId)
        this.passengers.push(obj)
      }
    })
    
  }
}



